
body {
  margin: 0px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  background: #8C9AFE;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  height: 100vh;
}
#container {
  overflow: hidden;
  -webkit-box-shadow: 0px 20px 70px #0002;
          box-shadow: 0px 20px 70px #0002;
  text-align: center;
  background: white;
  width: 400px;
}
#container .logo {
    width: 50px;
    padding: 1rem;
}
#container .inputs {
    padding: 1rem;
    font-size: 14px;
}
#container .inputs span {
      font-size: 12px;
}
#container .inputs * {
      margin: 2px;
}
#container .inputs div * {
      font-size: 1rem;
}
#container .inputs div select {
      width: 6rem;
}
#container #map {
    height: 120px;
}
#container .from {
    color: grey;
}



